.our-cases {
    @media (max-width: 576px) {
        &::after {
            content: "";
            position: absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            height: 50%;
            border-top: 1px solid rgba(22, 118, 170, 0.2);
            background: linear-gradient(180deg, rgba(5, 12, 20, 0.7) 0%, rgba(5, 12, 20, 0) 100%);
            filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.32));
        }
    }

    @media (max-width: 370px) {
        &::after {
            height: 52%;
        }
    }

    @media (max-width: 340px) {
        &::after {
            height: 53%;
        }
    }
}

.our-cases--cryptemper {
    padding-top: 16%;

    @media (max-width: 576px) {
        padding-top: 45px;
    }
}

.our-cases--twinby {
    padding-top: 18%;

    @media (max-width: 576px) {
        padding-top: 45px;

        &::after {
            height: 47.5%;
        }
    }

    @media (max-width: 370px) {
        &::after {
            height: 49.5%;
        }
    }

    @media (max-width: 340px) {
        &::after {
            height: 51.5%;
        }
    }
}

.our-cases--twinby .our-cases__img {
    width: 353px;
    height: 195px;

    @media (max-width: 992px) {
        width: 250px;
        height: 120px;
    }

    @media (max-width: 768px) {
        width: 100%;
        max-width: 342px;
        height: auto;
    }
}

.our-cases--twinby .our-cases__figcaption {
    color: #44FF78;
}

.our-cases__content {
    display: flex;

    @media (max-width: 768px) {
        flex-direction: column;
    }
}

.our-cases__text-content {
    max-width: 765px;
    margin-right: 110px;
    color: #fff;

    @media (max-width: 992px) {
        margin-right: 40px;
    }

    @media (max-width: 768px) {
        display: contents;
    }
}

.our-cases__descr {
    margin-top: 110px;

    @media (max-width: 1200px) {
        margin-top: 60px;
    }

    @media (max-width: 768px) {
        margin-top: 50px;
        order: 3;
        text-align: center;
    }

    @media (max-width: 576px) {
        position: relative;
        z-index: 10;
    }
}

.our-cases__figure {
    padding-top: 140px;

    @media (max-width: 1200px) {
        padding-top: 90px;
    }

    @media (max-width: 768px) {
        margin-top: 80px;
        padding-top: 0;
        order: 2;
    }
}

.our-cases__img {
    width: 354px;
    height: 176px;

    @media (max-width: 992px) {
        width: 250px;
        height: 120px;
    }

    @media (max-width: 768px) {
        display: block;
        margin: 0 auto;
        width: 100%;
        max-width: 342px;
        height: auto;
    }
}

.our-cases__figcaption {
    margin-top: 35px;

    text-align: center;
    font-weight: bold;
    font-size: 30px;
    line-height: 37px;
    color: #0064FF;

    @media (max-width: 992px) {
        margin-top: 20px;
    }

    @media (max-width: 768px) {
        margin-top: 30px;
        font-size: 26px;
        line-height: 32px;
    }
}

.our-cases__title {
    @media (max-width: 768px) {
        order: 1;
        text-align: center;
    }
}

.our-cases__link {
    @media (max-width: 768px) {
        align-self: center;
        order: 4;
    }
}