.container {
    display: block;
    margin: 0 auto;
    padding: 0 20px;
    width: 100%;
    max-width: 1360px;
}

@media screen and (max-width: 1400px) {
    .container {
        max-width: 1199px;
    }
}

@media screen and (max-width: 1200px) {
    .container {
        max-width: 990px;
    }
}

@media screen and (max-width: 992px) {
    .container {
        max-width: 760px;
    }
}

@media screen and (max-width: 768px) {
    .container {
        max-width: 570px;
    }
}

@media screen and (max-width: 576px) {
    .container {
        padding: 0 24px;
        width: 100%;
    }
}