.contact-us {
    padding-top: 15%;

    @media (max-width: 576px) {
        padding-top: 45px;
    }
}

.contact-us .screen__content {
    @media (max-width: 768px) {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}

.contact-us__form {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 50px;
    border: 1px solid rgba(22, 118, 170, 0.15);
    border-radius: 16px;
    padding: 32px 40px;
    max-width: 421px;
    background: rgba(5, 12, 20, 0.5);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.32);

    @media (max-width: 768px) {
        margin-top: 70px;
        width: 100%;
    }

    @media (max-width: 576px) {
        padding: 32px;
    }

    @media (max-width: 389px) {
        padding: 32px 16px;
    }
}

.contact-us__filed {
    display: block;
    margin-top: 16px;
    border: none;
    border-radius: 16px;
    padding: 14px 24px;
    width: 100%;
    max-width: 341px;

    font-weight: 600;
    font-size: 16px;
    line-height: 105%;
    letter-spacing: -0.015em;
    color: #fff;

    background: #081926;
}

.contact-us__filed::placeholder {
    color: rgba(255, 255, 255, 0.4);
}

.contact-us__filed:first-of-type {
    margin-top: 0;
}

.contact-us__btn {
    margin-top: 125px;
    border: 1px solid #1676AA;
    border-radius: 30px;
    padding: 15px 57px;
    background: none;

    font-weight: bold;
    font-size: 18px;
    line-height: 105%;
    text-align: center;
    letter-spacing: -0.015em;
    color: #fff;

    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));

    @media (max-width: 576px) {
        align-self: center;
        padding: 14px 56px;
    }
}

.contact-us__title {
    @media (max-width: 768px) {
        text-align: center;
    }
}