.footer {
    border-top: 1px solid rgba(#E6DFE6, 0.2);
    padding: 56px 0;
    background: #050C14;

    @media (max-width: 576px) {
        padding: 27px 0 40px;
        background: #050C14;
        filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.32));
    }
}

.footer__content {
    display: flex;
    align-items: center;

    @media (max-width: 992px) {
        flex-wrap: wrap;
        justify-content: center;
    }

    @media (max-width: 576px) {
        flex-wrap: nowrap;
        flex-direction: column;
        align-items: center;
    }
}

.footer__copyright {
    font-weight: normal;
    font-size: 18px;
    line-height: 105%;
    letter-spacing: -0.015em;
    color: #fff;
    opacity: 0.8;

    @media (max-width: 576px) {
        margin-top: 105px;
        order: 2;
    }
}

.footer__nav {
    margin-left: 170px;

    @media (max-width: 1200px) {
        margin-left: 65px;
    }

    @media (max-width: 992px) {
        margin-top: 40px;
        margin-left: 0;
        order: 1;
    }

    @media (max-width: 576px) {
        margin-top: 60px;
        order: 1;
    }
}

.footer__list {
    display: flex;
    align-items: center;

    @media (max-width: 576px) {
        flex-direction: column;
    }
}

.footer__item {
    margin-right: 50px;

    font-weight: bold;
    font-size: 18px;
    line-height: 105%;
    letter-spacing: -0.015em;
    color: #fff;

    @media (max-width: 1200px) {
        margin-right: 25px;
    }

    @media (max-width: 576px) {
        margin-right: 0;
        margin-bottom: 32px;
    }
}

.footer__item:last-of-type {
    margin-right: 0;

    @media (max-width: 576px) {
        margin-bottom: 0;
    }
}

.footer__logo {
    display: none;
    width: 217px;
    height: 46px;

    @media (max-width: 576px) {
        display: block;
    }
}

.footer__socials {
    margin-left: auto;
}

.socials {
    display: flex;
    align-items: center;

    @media (max-width: 576px) {
        display: none;
    }
}

.socials__item {
    display: inline-flex;
    margin-right: 24px;

    &--fb {
        width: 12px;
        height: 23px;
    }

    &--yt {
        width: 26px;
        height: 18px;
    }
    
    &--inst {
        width: 24px;
        height: 24px;
    }

    &--tw {
        width: 26px;
        height: 22px;
    }
}

.socials__item:last-of-type {
    margin-right: 0;
}

.socials__link svg {
    width: 100%;
    height: 100%;
}