.title  {
    font-weight: 400;
    font-size: 50px;
    line-height: 52px;
    letter-spacing: -0.015em;
    color: #fff;

    @media (max-width: 1200px) {
        font-size: 42px;
        line-height: 44px;
    }

    @media (max-width: 576px) {
        font-size: 36px;
        line-height: 37px;
    }
}

.text {
    font-weight: normal;
    font-size: 32px;
    line-height: 34px;
    letter-spacing: -0.015em;
    color: #fff;

    @media (max-width: 1200px) {
        font-size: 26px;
        line-height: 28px;
    }

    @media (max-width: 576px) {
        font-size: 22px;
        line-height: 23px;
    }
}