.link {
    display: inline-flex;
    align-items: center;
    margin-top: 40px;
    padding: 10px 0;
}

.link--underlined {
    .link__text {
        border-bottom: 1px solid rgba(#D1F2FF, 0.5);
    }

    @media (max-width: 576px) {
        .link__cross {
            display: none;
        }

        .link__text {
            position: relative;
            display: inline-block;
            border-bottom: none;

            &::after {
                content: "";
                position: absolute;
                bottom: -2px;
                left: 0;
                width: 100%;
                height: 1px;
                background: #fff;
                box-shadow: 0 0 2px 1px var(--link-glow);
            }
        }
    }
}

.link__cross {
    position: relative;
    margin-right: 17px;
    width: 21px;
    height: 21px;
}

.link__bar {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #fff;
    box-shadow: 0 0 3px 1px var(--link-glow);
}

.link__bar--hrz {
    width: 100%;
    height: 1px;
}

.link__bar--vrl {
    height: 100%;
    width: 1px;
}

.link__text {
    font-weight: bold;
    font-size: 22px;
    line-height: 23px;
    letter-spacing: -0.015em;
    color: #fff;
}