.header__content {
    padding-top: 25px;
    display: flex;
    justify-content: space-between;

    @media (max-width: 768px) {
        align-items: center;
    }

    @media (max-width: 576px) {
        padding-top: 15px;
    }
}

.header__nav {
    padding-top: 5px;
    display: flex;
    color: #fff;

    @media (max-width: 576px) {
        padding-top: 40px;
        flex-direction: column;
    }
}

.header__link {
    display: block;
    padding: 15px;
    font-weight: bold;
    font-size: 18px;
    line-height: 19px;
    text-align: right;
    letter-spacing: -0.015em;

    @media (max-width: 768px) {
        padding: 5px;
    }

    @media (max-width: 576px) {
        margin-bottom: 10px;
        padding: 10px 20px;
        text-align: center;
    }
}

.header__link--framed {
    margin-left: 5px;
    border: 1px solid #1676AA;
    border-radius: 30px;
    padding: 14px 30px;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));

    @media (max-width: 768px) {
        padding: 5px 10px;
    }

    @media (max-width: 576px) {
        margin-bottom: 0;
        padding: 10px 20px;
        text-align: center;
    }
}

.logo {
    width: 223px;
    height: 47px;

    @media (max-width: 576px) {
        width: 180px;
        height: 38px;
    }
}

.logo img {
    width: 100%;
    height: 100%;
    max-width: 100%;
}

.close {
    display: none;

    @media (max-width: 576px) {
        position: relative;
        display: block;
        border: 1px solid #fff;
        width: 40px;
        height: 40px;
        background: none;
        cursor: pointer;
    }

    &::before,
    &::after {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        border-radius: 2px;
        width: 20px;
        height: 2px;

        background: #fff;
    }

    &::before {
        transform: translate(-50%, -50%) rotate(-45deg);
    }

    &::after {
        transform: translate(-50%, -50%) rotate(45deg);
    }
}

.hamburger {
    display: none;

    @media (max-width: 576px) {
        display: flex;
        flex-direction: column;
        align-items: center;
        border: 1px solid #0096D7;
        border-radius: 50%;
        padding: 12px 0 0;
        width: 41px;
        height: 41px;
        background: none;
        cursor: pointer;
    }

    &__bar {
        display: block;
        margin-top: 5px;
        border-radius: 2px;
        width: 21px;
        height: 2px;
        background: #fff;

        &:first-of-type {
            margin-top: 0;
        }
    }
}

.header__menu {
    @media (max-width: 576px) {
        position: absolute;
        top: 0;
        right: -100%;
        z-index: 100;
        height: 100vh;
        width: 60%;
        background: rgba(0, 0, 0, 0.801);
        opacity: 0;
        visibility: hidden;
        transition: all 0.4s ease-in;
    }

    @media (max-width: 390px) {
        width: 75%;
    }
}

.header__menu--active {
    right: 0;
    opacity: 1;
    visibility: visible
}