.for-investors {
    padding-top: 13%;

    @media (max-width: 576px) {
        padding-top: 45px;
    }
}

.for-investors__title {
    text-align: center;
}

.for-investors__text {
    text-align: center;
}

.for-investors__text-content {
    margin: 0 auto;
    margin-top: 150px;
    max-width: 875px;

    @media (max-width: 1200px) {
        margin-top: 100px;
    }

    @media (max-width: 768px) {
        margin-top: 80px;
    }
}

.for-investors__text:last-of-type {
    margin-top: 52px;

    @media (max-width: 768px) {
        margin-top: 30px;
    }
}