.hero {
    padding-top: 180px;
    display: flex;
    justify-content: space-between;

    @media (max-width: 1200px) {
        padding-top: 100px;
    }

    @media (max-width: 992px) {
        padding-top: 70px;
        flex-direction: column;
    }

    @media (max-width: 576px) {
        padding-top: 70px;
    }
}

.hero__text-content {
    padding-top: 200px;

    @media (max-width: 992px) {
        padding-top: 20px;
    }

    @media (max-width: 768px) {
        padding-top: 40px;
    }

    @media (max-width: 576px) {
        padding-top: 60px;
    }
}

.hero__slider-wrapper {
    position: relative;
    padding-left: 48px;
    max-width: 450px;

    @media (max-width: 576px) {
        padding-left: 0;
    }
}

.hero-slider__slide {
    position: relative;
    display: flex;
}

.hero-slider__content {
    display: flex;

    @media (max-width: 1400px) {
        justify-content: space-between;
    }
}

.hero-slider__title {
    display: flex;

    font-weight: normal;
    font-size: 22px;
    line-height: 23px;
    letter-spacing: -0.015em;
    color: #fff;

    @media (max-width: 576px) {
        flex-direction: column;
        align-items: center;
        text-align: center;
    }
}

.hero-slider__title strong {
    margin-right: 25px;
    font-weight: normal;
    font-size: 85px;
    line-height: 67px;
    letter-spacing: -0.015em;
    color: #1EA4DE;

    @media (max-width: 576px) {
        margin-right: 0;
    }
}

.hero-slider__title span {
    opacity: 0.8;

    @media (max-width: 576px) {
        margin-top: 10px;
    }
}

.hero__img {
    width: 702px;
    height: 578px;
    user-select: none;

    @media (max-width: 1400px) {
        width: 600px;
        height: 500px;
    }

    @media (max-width: 1200px) {
        margin-top: 85px;
        width: 420px;
        height: 340px;
    }

    @media (max-width: 992px) {
        order: -1;
        align-self: flex-end;
        margin-top: 0;
    }

    @media (max-width: 768px) {
        width: 389px;
        height: 320px;
    }

    @media (max-width: 576px) {
        position: relative;
        right: -90px;

        width: 389px;
        height: 320px;

        opacity: 0.2;
        transform: rotate(-12deg);
    }
}

.hero__img img {
    width: 100%;
    height: 100%;
    max-width: 100%;
}

.hero-slider__pagination {
    position: absolute;
    top: 0;
    left: 0;
    transform: translateY(6%);

    li {
        margin-top: 12px;
    }

    li:first-of-type {
        margin-top: 0;
    }

    li.slick-active button::after {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 10px;
        height: 10px;
        background: radial-gradient(50% 50% at 50% 50%, #FFFFFF 51.04%, rgba(255, 255, 255, 0) 100%);
    }

    button {
        // hide text
        position: relative;
        overflow: hidden;
        text-indent: -9999px;
        white-space: nowrap;

        display: block;
        border: 1px solid rgba(255, 255, 255, 0.8);
        border-radius: 50%;
        width: 14px;
        height: 14px;

        background: none;
        cursor: pointer;
    }

    @media (max-width: 576px) {
        height: 100px;
        left: 50%;
        top: 100%;
        transform: translateX(-50%) rotate(-90deg);

        li {
            margin-top: 26px;
        }

        button {
            width: 16px;
            height: 16px;
        }

        li.slick-active button::after {
            background: #fff;
            border-radius: 50%;
            width: 8px;
            height: 8px;
        }
    }
}

.hero__link {
    margin-top: 50px;

    @media (max-width: 992px) {
        margin-top: 40px;
    }

    @media (max-width: 576px) {
        display: none;
    }
}