.screen {
    position: relative;
    min-height: 100vh;
    background: #050C14;
}

.screen--header {
    @media (max-width: 576px) {
        overflow: hidden;
    }
}

.screen--cut {
    min-height: 992px;

    @media (max-width: 576px) {
        min-height: 710px;
    }
}

.screen__content {
    position: relative;
    z-index: 10;
}